import { Component, OnInit, ViewChild } from '@angular/core';
import { CheckServiceService } from '../services/check-service.service';
import { MatTableDataSource } from '@angular/material';
import { ICheck } from '../interfaces';
import { MatPaginator } from '@angular/material/paginator';
import { DatePipe } from '@angular/common';
import { FormControl, FormGroup } from '@angular/forms';

import { DateAdapter, MAT_DATE_FORMATS } from "@angular/material";
import { MAT_DATE_LOCALE } from "@angular/material/core";
import { AppDateAdapter, APP_DATE_FORMATS } from './date.adapter';

@Component({
  selector: 'app-operation',
  templateUrl: './operation.component.html',
  styleUrls: ['./operation.component.less'],
  providers: [
    {
      provide: DateAdapter, useClass: AppDateAdapter
    },
    {
      provide: MAT_DATE_FORMATS, useValue: APP_DATE_FORMATS
    },
    {
      provide: MAT_DATE_LOCALE, useValue: 'ru-RU' //календарь на русском
    }
  ]
})

export class OperationComponent implements OnInit {
  title = 'Операции';

  dataSourceChecks: MatTableDataSource<ICheck>;
  displayedColumnsItems: string[] = ['date', 'time', 'amount', 'received_at', 'status'];
  items: Array<ICheck>;

  constructor(
    private cServ: CheckServiceService,
    private datePipe: DatePipe,

  ) {
    this.fillTableOperations();

    /* фильтры по сумме и дате */
    this.pipe = new DatePipe('en');
    this.dataSourceChecks.filterPredicate = (data, filter) => {
      if (this.fromDate && this.toDate) { //по дате
        return data.date >= this.fromDate.toLocaleDateString() && data.date <= this.toDate.toLocaleDateString();
      }
      if (this.fromSum && this.toSum) { //по сумме
        return data.amount >= this.fromSum && data.amount <= this.toSum;
      }
      return true;
    }

    /* /фильтры по сумме и дате */

  }




  fillTableOperations() {
    this.items = this.cServ.getChecks();
    this.dataSourceChecks = new MatTableDataSource(this.items.reverse());
  }

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;

  ngOnInit() {
    this.dataSourceChecks.paginator = this.paginator;
  }

  /*search(filterValue: string) { //отключаю, т.к. при попытке поиска вызываются get fromSum() и get toSum() и TypeError: Cannot read property 'toString' of undefined"
    this.dataSourceChecks.filter = filterValue.trim().toLowerCase();
    if (this.dataSourceChecks.paginator) {
      this.dataSourceChecks.paginator.firstPage();
    }
  }*/

  applyFilter(filterValue: string) { //фильтр по статусу
    switch (filterValue) {
      case 'loaded': //Чек проверяется 
        // console.log(filterValue);
        this.dataSourceChecks.filterPredicate =
          (data: ICheck, filter: string) => !filter || data.cashback.status.toLowerCase().trim() == filter.toLowerCase().trim();
        this.dataSourceChecks.filter = filterValue;
        if (this.dataSourceChecks.paginator) { this.dataSourceChecks.paginator.firstPage(); }
        break;

      case 'cashback_false': //Чек без кэшбэка
        this.dataSourceChecks.filterPredicate =
          (data: ICheck, filter: string) => !filter || data.cashback.status.toLowerCase().trim() == 'no_cashback';
        this.dataSourceChecks.filter = filterValue;
        if (this.dataSourceChecks.paginator) { this.dataSourceChecks.paginator.firstPage(); }
        break;

      case 'cashback_true': //Чек с кэшбэком
        this.dataSourceChecks.filterPredicate =
          (data: ICheck, filter: string) => !filter || data.cashback.status.toLowerCase().trim() != 'no_cashback';
        this.dataSourceChecks.filter = filterValue;
        if (this.dataSourceChecks.paginator) { this.dataSourceChecks.paginator.firstPage(); }
        break;

      case 'all': //Все чеки
      default:
        this.fillTableOperations();
    }
  }

  //фильтр по дате
  pipe: DatePipe;
  filterFormDate = new FormGroup({
    fromDate: new FormControl(),
    toDate: new FormControl(),
  });

  get fromDate() { return this.filterFormDate.get('fromDate').value; }
  get toDate() { return this.filterFormDate.get('toDate').value; }
  // today = new FormControl(new Date());

  applyFilterDate() { //применить фильтр по дате
    if (this.fromDate && this.toDate) {
      this.dataSourceChecks.filter = '' + Math.random();
      console.log(this.fromDate.toLocaleDateString());
      console.log(this.toDate.toLocaleDateString());
    }
    else {
      console.log('Поля не заполнены');
    }
  }
  // /фильтр по дате

  // фильтр по сумме
  filterFormSum = new FormGroup({
    fromSum: new FormControl(),
    toSum: new FormControl(),
  });

  get fromSum() { return this.filterFormSum.get('fromSum').value.toString(); }
  get toSum() { return this.filterFormSum.get('toSum').value.toString(); }
  applyFilterSum() { //применить фильтр по сумме
    if (this.fromSum && this.toSum) {
      this.dataSourceChecks.filter = '' + Math.random();
      console.log(this.fromSum);
      console.log(this.toSum);
    }
    else {
      console.log('Поля не заполнены');
    }
  }
  // /фильтр по сумме



  resetFilters() {
    var radio = document.getElementsByTagName('mat-radio-button'); //найти все радиокнопки
    for (var i = 0; i < radio.length; i++) {
      radio[i].classList.remove('mat-radio-checked'); //удалить checked
    }

    var field = document.getElementsByTagName('input'); //найти все input
    for (var i = 0; i < field.length; i++) {
      field[i].value = ''; //удалить value
    }

    this.fillTableOperations(); //все чеки
  }








}