import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CheckServiceService } from '../services/check-service.service';
import { MatTableDataSource } from '@angular/material';
import { distinctCheck, distinctCheckGoods, distinctCheckItemsWithCashback } from '../interfaces';

@Component({
    selector: 'app-check-details',
    templateUrl: './check-details.component.html',
    styleUrls: ['./check-details.component.less']
})
export class CheckDetailsComponent implements OnInit {
    title = 'Операции';
    itemsFromCheck: Array<distinctCheckGoods>;
    item: distinctCheck;

    dataSourceCheck: MatTableDataSource<distinctCheckGoods>;
    displayedColumnsCheck: string[] = ['name', 'quantity', 'rate', 'amount', 'id'];

    itemsFromCheck2: Array<distinctCheckItemsWithCashback>; //товары с кэшбэком
    dataSourceCheck2: MatTableDataSource<distinctCheckItemsWithCashback>;
    displayedColumnsCheck2: string[] = ['receipt_item_id'];

    constructor(
        private cServ: CheckServiceService,
        private route: ActivatedRoute,
    ) {
    }


    ngOnInit() {
        this.getCheck();
    }

    findGoods(obj) {
        let arr = [];
        obj.goods.map(element => {
            arr.push(element)
        });
        this.itemsFromCheck = arr;
    }

    findCheck(items, id): void {
        let lobj = items.find((it: distinctCheck) => it.id === id);
        this.item = lobj;
        this.findGoods(lobj)
    }


    findGoods2(obj) { //товары с кэшбэком
        let arr = [];
        obj.items.map(element => {
            arr.push(element)
        });
        this.itemsFromCheck2 = arr;
    }
    findCheck2(items, id): void { //товары с кэшбэком
        let lobj2 = items.find((it: distinctCheck) => it.id === id);
        this.item = lobj2;
        this.findGoods2(lobj2)
    }

    getCheck(): void {
        const id = +this.route.snapshot.paramMap.get('id');
        this.findCheck(this.cServ.getChecks(), id);
        this.dataSourceCheck = new MatTableDataSource(this.itemsFromCheck);

        this.findCheck2(this.cServ.getChecks(), id); //товары с кэшбэком
        this.dataSourceCheck2 = new MatTableDataSource(this.itemsFromCheck2);
    }

}