import { Component, OnInit, ViewChild } from '@angular/core';
import { ItemsServiceService } from '../services/items-service.service';
import { MatTableDataSource } from '@angular/material';
import { IItem } from '../interfaces';
import { MatPaginator } from '@angular/material/paginator';
import { DatePipe } from '@angular/common';
import { FormControl, FormGroup } from '@angular/forms';
import { FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-sale',
  templateUrl: './sale.component.html',
  styleUrls: ['./sale.component.less'],
})

export class SaleComponent implements OnInit {
  title = 'Акции';

  dataSourceItems: MatTableDataSource<IItem>;
  displayedColumnsItems: string[] = ['images', 'title', 'cashback']; //для отображения даты добавить 'date_to'

  filterShowCompleted = new FormGroup({
    showCompleted: new FormControl(),
  });

  constructor(
    private itServ: ItemsServiceService,
    fb: FormBuilder
  ) {
    this.filterShowCompleted = fb.group({//флаг показать завершенные акции нажат по умолчанию
      showCompleted: true,
    });
  }

  fillTableItems() {
    const items: Array<IItem> = this.itServ.getItems();
    this.dataSourceItems = new MatTableDataSource(items.slice(-10).reverse()); //10 последних товаров
  }

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;

  ngOnInit() {
    this.fillTableItems();
    this.dataSourceItems.paginator = this.paginator;
  }

  search(filterValue: string) {
    this.dataSourceItems.filter = filterValue.trim().toLowerCase();
    if (this.dataSourceItems.paginator) {
      this.dataSourceItems.paginator.firstPage();
    }
  }

  categories = [
    {
      name: 'Все',
      id: 0
    },
    {
      name: 'Чай, кофе, какао',
      id: 1
    },
    {
      name: 'Макароны, крупы, мука',
      id: 2
    },
    {
      name: 'Овощные консервы',
      id: 3
    }
  ]

  changeToggle2() {
    console.log("changeToggle2!");
  }

  applyFilter(filterValue: string) {
    this.fillTableItems(); //все товары

    if (filterValue != 'Все') { //фильтр по категориям
      console.log(filterValue);
      this.dataSourceItems.filterPredicate =
        (data: IItem, filter: string) => !filter || data.category.name.toLowerCase().trim() == filter.toLowerCase().trim();
      this.dataSourceItems.filter = filterValue;

      if (this.dataSourceItems.paginator) {
        this.dataSourceItems.paginator.firstPage();
      }
    }

  }


  get showCompleted() { return this.filterShowCompleted.get('showCompleted').value.toString(); }
  today = new Date().toISOString(); //текущая дата в формате iso 2019-09-04T07:38:46.407Z

  showCompletedSale() {//показать завершенные акции
    this.dataSourceItems.filterPredicate = (data, filter) => {
      if (this.showCompleted) {
        return data.date_to <= this.today; //завершенные акции
      }
      return true;
    }

    if (this.showCompleted) {
      this.dataSourceItems.filter = '' + Math.random();
      console.log('showCompleted:', this.showCompleted);
    }

  }






}
